import React from "react";
import '../styles/Eventboard.scss';
import { Link } from 'react-router-dom';


export default function Eventboard() {
    return <div id="event_main">
        <div id="event_maincontainer">
            <p class="event_first_text"></p>
            <ul class="event_contents">
                <li><Link to="#"><img src={require('../assets/eventboard_img/event (8).png')} alt="  10% " /></Link></li>
                <li><Link to="#"><img src={require('../assets/eventboard_img/event (1).png')} alt="    " /></Link></li>
                <li><Link to="#"><img src={require('../assets/eventboard_img/event (2).png')} alt="    " /></Link></li>
                <li><Link to="#"><img src={require('../assets/eventboard_img/event (3).png')} alt=" 18   " /></Link></li>
                <li><Link to="#"><img src={require('../assets/eventboard_img/event (4).png')} alt="    " /></Link></li>
                <li><Link to="#"><img src={require('../assets/eventboard_img/event (5).png')} alt="  set 42,000" /></Link></li>
                <li><Link to="#"><img src={require('../assets/eventboard_img/event (6).png')} alt=" 450ml 13,000" /></Link></li>
                <li><Link to="#"><img src={require('../assets/eventboard_img/event (7).png')} alt=" 500ml 1,600" /></Link></li>
            </ul>
        </div>
    </div>
}