import React, {useEffect, useState} from 'react';
import '../styles/FAQ.scss';
import { Link } from 'react-router-dom';

import { BoardTitle, BoardSearch, BoardScope } from './Boardtable';
import axios from "axios";

function FAQ() {
    const [faqlist, setFaqlist] = useState([]);

    useEffect(() => {
        axios
            .get('/flist')
            .then((response) => {
                setFaqlist(response.data);
            })
            .catch((error) => {
                console.log(error);
            });
    }, []);

    return (
        <div id="Board_main">
            <div id="board_maincontainer">
                <BoardTitle page={page} />
                <FaqTitle />

                <div>
                    <FaqContainer />
                    <BoardSearch />
                    <BoardScope page={page} items={faqlist} />
                </div>
            </div>
        </div>
    );

    function FaqTitle() {
        return (
            <>
                <div className="FAQ_text">
                    <p>
                              
                           .
                        <br />
                               &gt;
                         ~
                    </p>
                </div>

                <div>
                    <div className="customer_inquiry">
                        <ul>
                            <li className="customer_inquiry_button">
                                <Link to="/simpleinquiry">
                                     
                                </Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </>
        );
    }

    function FaqContainer() {
        return (
            <div className="Qbox_container">
                <ul>
                    {faqContainer.map((el) => (
                        <li className="Qbox">
                            {el.Question}
                            <ul className="Abox">
                                <li>
                                    <p>{el.Answer}</p>
                                </li>
                            </ul>
                        </li>
                    ))}
                </ul>
            </div>
        );
    }
}

const page = {
    title: 'faq',
    korTitle: 'FAQ',
    scope: ['', ' ', ' '],
};

const items = [
    {
        scope: '1',
        title: 'faq1',
        contents: 'faq1',
    },
    {
        scope: '2',
        title: 'faq2',
        contents: 'faq2',
    },
    {
        scope: '3',
        title: 'faq3',
        contents: 'faq3',
    },
];

const faqContainer = [
    {
        Question: '   .',
        Answer: '   /    .',
    },
    {
        Question: '   .',
        Answer: '    7    .',
    },
    {
        Question: '     ?',
        Answer: '                 .',
    },
];

export default FAQ;
