import React from 'react';
import '../styles/Simple_inquiry.scss';
import { Link } from 'react-router-dom';

function Simpleinquiry() {
    return (
        <div id="simple_inquiry">
            <div id="simple_inquiry_container">
                <p className="main_first_text"></p>

                <div className="main_text">
                    <p>
                        :  08:30 ~  17:30  :  12:00 ~
                         13:00  : , ,  
                        <br />
                           ,    
                         .
                    </p>

                    <div>
                        <span></span>
                        <strong>070-1111-2222</strong>
                    </div>
                </div>

                <UploadForm onSubmit={onSubmit} />

            </div>
        </div>
    );
}

function onSubmit() {

}

function UploadForm({ onSubmit }) {
    return (
        <form onSubmit={onSubmit} className="simple_inquirybox">
            <div className="contens">
                <p>
                    <span>*</span>  
                </p>
                <figure>
                    <table>
                        <colgroup>
                            <col />
                            <col />
                        </colgroup>

                        <tbody>
                            <tr>
                                <th>
                                    <span>&#42;</span>
                                </th>
                                <td>
                                    <label>
                                        <input
                                            type="radio"
                                            name="contens"
                                            value="purchase"
                                            checked
                                        />
                                         
                                    </label>

                                    <label>
                                        <input
                                            type="radio"
                                            name="contens"
                                            value="product"
                                        />
                                         
                                    </label>

                                    <label>
                                        <input
                                            type="radio"
                                            name="contens"
                                            value="event"
                                        />
                                         
                                    </label>

                                    <label>
                                        <input
                                            type="radio"
                                            name="contens"
                                            value="homepage"
                                        />
                                         
                                    </label>

                                    <label>
                                        <input
                                            type="radio"
                                            name="contens"
                                            value="shipping_inquiry"
                                        />
                                         
                                    </label>

                                    <label>
                                        <input
                                            type="radio"
                                            name="contens"
                                            value="shipping_complaint"
                                        />
                                         
                                    </label>
                                </td>
                            </tr>

                            <tr>
                                <th>
                                    <span>&#42;</span>
                                </th>
                                <td>
                                    <input
                                        type="text"
                                        minlength="2"
                                        maxlength="10"
                                        name="userName"
                                        placeholder="  ."
                                        required
                                    />
                                </td>
                            </tr>

                            <tr>
                                <th>
                                    <span>&#42;</span>
                                </th>
                                <td>
                                    <input
                                        type="password"
                                        name="inquiryPSW"
                                        minlength="3"
                                        maxlength="4"
                                        size="20px"
                                        placeholder="  ."
                                        required
                                    />
                                </td>
                            </tr>

                            <tr>
                                <th>
                                    <span>&#42;</span>
                                </th>
                                <td>
                                    <input
                                        type="text"
                                        name="Title"
                                        maxlength="50"
                                        size="60"
                                        minlength="5"
                                        placeholder="  ."
                                        required
                                    />
                                </td>
                            </tr>

                            <tr>
                                <th>
                                    <span>&#42;</span>
                                </th>
                                <td>
                                    <textarea
                                        name="inquiries"
                                        cols="130"
                                        rows="20"
                                        minlength="10"
                                        placeholder="  ."
                                        required
                                    ></textarea>
                                </td>
                            </tr>

                            <tr>
                                <th>
                                    <span>&#42;</span>
                                </th>
                                <td>
                                    <input
                                        type="text"
                                        name="userPostcode1"
                                        size="10px"
                                        placeholder=""
                                        required
                                    />
                                    <Link to="#"></Link>
                                    <br />
                                    <input
                                        type="text"
                                        name="userPostcode2"
                                        size="50"
                                        placeholder=""
                                        required
                                    />
                                    <span></span>
                                    <br />
                                    <input
                                        type="text"
                                        name="userPostcode3"
                                        size="50"
                                        placeholder=" "
                                    />
                                    <span> ()</span>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </figure>
            </div>

            <div className="agree_submit">
                <Link to="/customerpage"></Link>
                <button type="submit"></button>
            </div>
        </form>
    )
}

export { Simpleinquiry, UploadForm };