import React, { useEffect, useState } from 'react';
import '../../styles/JoinForm.scss';
import axios from 'axios';
import {Link, useNavigate} from 'react-router-dom';
import Popup from "./Popup";
import PopupPost from "./PopupPost";



function JoinForm() {
    return (
        <>
            <div id="joinform_container">
                <p className="pageTitle"></p>
                <div className="contents">
                    <JoinBox />
                </div>
            </div>
        </>
    );
}

// DB   (form   db )
const fetchData = async (userData)=>{
    try {
        console.log(userData);
        const response = await axios.post("/user/join",userData);
        alert(" .");
        return response.data;
    }catch{
        alert(" .     .");
    }
};
// ID  
const checkid = (e)=>{
    e.preventDefault();
    axios.get("/user/checkid")
        .then((response)=>{
            console.log(response.data);
        })
        .catch()
}

const JoinBox = () => {
    //    
    const [userData, setUserData] = useState({});
    //   
    const [phoneNum, setPhoneNum] = useState({
        firstNum: '',
        secondNum: '',
        lastNum: '',
    });
    const navigate = useNavigate();

    //       - 1/2
    const phoneFunc = (e) => {
        //     input   
        const { name, value } = e.target;
        setPhoneNum((phoneData) => ({
            ...phoneData,
            [name]: value,
        }));
    };

    //       - 2/2
    useEffect(() => {
        //            
        setUserData((data) => ({
            ...data,
            phone: `${phoneNum.firstNum}-${phoneNum.secondNum}-${phoneNum.lastNum}`,
        }));
    }, [phoneNum]);

    //    
    const handleChange = (e) => {
        // DB   
        const { name: varName, value: varValue } = e.target;
        setUserData((data) => ({
            ...data,
            [varName]: varValue,
        }));
    };

    //   
    const dataSubmit = async (e) => {
        e.preventDefault();
        await fetchData(userData);
        if( window.confirm("  ?")){
            navigate("/login");
        }else{
            navigate("/");
        }
    };

    // useEffect(()=>{
    //     if( window.confirm("  ?")){
    //         navigate("/login");
    //     }else{
    //         navigate("/");
    //     }
    // },[dataSubmit])

    //      
    const [completeVal, setCompletVal]= useState({});
    //    
    const [valText, setValText]= useState({
    });
    //   
    const validation = (e)=>{
        const {id, value}=e.target;
        switch (id) {
            case "userID" :
                if (value === '') {
                    setValText((prev)=>({
                        ...prev,
                        [id] : " ."
                    }));
                } else if (/^(?=.*[a-z])(?=.*[0-9])[a-z0-9]{8,15}$/g.test(value)) {
                    setValText((prev)=>({
                        ...prev,
                        [id] : " ."
                    }));
                    setCompletVal((prev)=>({
                        ...prev,
                        [id]: null,
                    }));
                } else {
                    setValText((prev)=>({
                        ...prev,
                        [id] : ",   8  15  ."
                    }));
                }
                break;
            case "userPSW" :
                if (value === '') {
                    setValText((prev)=>({
                        ...prev,
                        [id] : " ."
                    }));
                } else if (/^(?=.*[a-z])(?=.*[0-9])(?=.*[!@#$%^&*])[a-z0-9!@#$%^&*]{8,15}$/g.test(value)) {
                    setValText((prev)=>({
                        ...prev,
                        [id] : " ."
                    }));
                    setCompletVal((prev)=>({
                        ...prev,
                        [id]: null,
                    }));
                } else {
                    setValText((prev)=>({
                        ...prev,
                        [id] : ", ,   8 15  ."
                    }));
                }
                break;
            case "rePSW" :
                if(userData.password === value){
                    setValText((prev)=>({
                        ...prev,
                        [id] : " ."
                    }));
                    setCompletVal((prev)=>({
                        ...prev,
                        [id]: null,
                    }));
                }else{
                    setValText((prev)=>({
                        ...prev,
                        [id] : "  ."
                    }));
                }
                break;
            case "passRequest" :
                break;
            case "ckpwa" :
                break;
            case "userName" :
                if(/^[-]{2,5}$/.test(value)){
                    setValText((prev)=>({
                        ...prev,
                        [id] : null
                    }));
                }else{
                    setValText((prev)=>({
                        ...prev,
                        [id] : " 2  5  ."
                    }));
                }
                break;
            case "firstNum" :
                if(typeof value === "number"){
                    setValText((prev)=>({
                        ...prev,
                        [id] : null
                    }));
                }else{
                    setValText((prev)=>({
                        ...prev,
                        [id] : "   ."
                    }));
                }
                break;
            case "secondNum" :
                break;
            case "thirdNum" :
                break;
            case "email" :
                break;


        }
    }
    const [isPopupOpen, setIsPopupOpen] = useState(false);
    const [popupPostData, setPopupPostData] = useState([]);
    //  
    const openPostCode = (e) => {
        e.preventDefault();
        setIsPopupOpen(true)
    }

    //  
    const closePostCode = () => {
        setIsPopupOpen(false)
    }


    //    
    const handlePopupData = (data) => {
        console.log("joinForm : "+data);
        setPopupPostData(data);
        console.log(popupPostData);
    };

    return (
        <>
            <form onSubmit={dataSubmit} className="joinbox" method="post">
                <p>
                    <span>*</span>   
                </p>
                <figure>
                    <figcaption></figcaption>
                    <table>
                        <colgroup>
                            <col />
                            <col />
                        </colgroup>

                        <tbody>
                            <tr>
                                <th>
                                     <span>*</span>
                                </th>
                                <td>
                                    <input
                                        type="text"
                                        minLength="4"
                                        maxLength="16"
                                        name="id"
                                        id="userID"
                                        // placeholder=",   8  15  ."
                                        onChange={handleChange}
                                        onBlur={validation}
                                    />
                                    <button onClick={checkid} ></button>
                                    {valText.userID&&<div>{valText.userID}</div>}
                                </td>
                            </tr>
                            <tr>
                                <th>
                                     <span>*</span>
                                </th>
                                <td>
                                    <input
                                        type="password"
                                        minLength="6"
                                        maxLength="16"
                                        name="password"
                                        id="userPSW"
                                        // placeholder=", ,   8 15  ."
                                        onChange={handleChange}
                                        onBlur={validation}
                                    />
                                    <span>

                                    </span>
                                    {valText.userPSW&&<div>{valText.userPSW}</div>}
                                </td>
                            </tr>
                            <tr>
                                <th>
                                      <span>*</span>
                                </th>
                                <td>
                                    <input
                                        type="password"
                                        minLength="6"
                                        maxLength="16"
                                        id="rePSW"
                                        onBlur={validation}
                                    />
                                    {valText.rePSW&&<div className="confirmPSW notice">{valText.rePSW}</div>}
                                </td>
                            </tr>
                            <tr>
                                <th>
                                       <span>*</span>
                                </th>
                                <td>
                                    <select
                                        id = "passRequest"
                                        name="password_q"
                                        onChange={handleChange}
                                        onBlur={validation}
                                    >
                                        <option value="pswQ1">
                                               ?
                                        </option>
                                        <option value="pswQ2">
                                              ?
                                        </option>
                                        <option value="pswQ3">
                                               1?
                                        </option>
                                        <option value="pswQ4">
                                                ?
                                        </option>
                                        <option value="pswQ5">
                                                
                                            ?
                                        </option>
                                        <option value="pswQ6">
                                               ?
                                        </option>
                                        <option value="pswQ7">
                                                 
                                            ?
                                        </option>
                                        <option value="pswQ8">
                                                ?
                                        </option>
                                        <option value="pswQ9">
                                                ?
                                        </option>
                                        <option value="pswQ10">
                                                
                                            ?
                                        </option>
                                        <option value="pswQ11">
                                                ?
                                        </option>
                                        <option value="pswQ12">
                                                
                                             ?
                                        </option>
                                        <option value="pswQ13">
                                                 ?
                                        </option>
                                        <option value="pswQ14">
                                                ?
                                        </option>
                                        <option value="pswQ15">
                                              ?
                                        </option>
                                    </select>
                                </td>
                            </tr>
                            <tr>
                                <th>
                                       <span>*</span>
                                </th>
                                <td>
                                    <input
                                        type="text"
                                        name="password_a"
                                        size="70px"
                                        id="ckpwa"
                                        onChange={handleChange}
                                        onBlur={validation}
                                    />
                                </td>
                            </tr>
                            <tr>
                                <th>
                                     <span>*</span>
                                </th>
                                <td>
                                    <input
                                        type="text"
                                        minLength="2"
                                        maxLength="10"
                                        name="name"
                                        id="userName"
                                        onChange={handleChange}
                                        onBlur={validation}
                                    />
                                    {valText.userName&&<div>{valText.userName}</div>}
                                </td>
                            </tr>
                            <tr>
                                <th></th>
                                <td>
                                    <input
                                        type="text"
                                        name="postal"
                                        size="10px"
                                        value={popupPostData[1]}
                                        onChange={handleChange}
                                    />
                                    <button onClick={openPostCode}></button>
                                    <div id="popupDom">
                                        {isPopupOpen && (
                                            <Popup>
                                                <PopupPost onPopupData={handlePopupData} onClose={closePostCode} />
                                            </Popup>
                                        )}
                                    </div>
                                    <br />
                                    <input
                                        type="text"
                                        name="address_1"
                                        size="50"
                                        value={popupPostData[0]}
                                        onChange={handleChange}
                                    />
                                    <span></span>
                                    <br />
                                    <input
                                        type="text"
                                        name="address_2"
                                        size="50"
                                        id="address"
                                        onChange={handleChange}
                                    />
                                    <span> ()</span>
                                </td>
                            </tr>
                            <tr>
                                <th>
                                     <span>*</span>
                                </th>
                                <td>
                                    <select
                                        id ="firstNum"
                                        name="firstNum"
                                        onChange={phoneFunc}
                                    >
                                        <option value=""></option>
                                        <option value="010">010</option>
                                        <option value="011">011</option>
                                        <option value="016">016</option>
                                        <option value="017">017</option>
                                        <option value="018">018</option>
                                        <option value="019">019</option>
                                    </select>
                                    -
                                    <input
                                        type="text"
                                        name="secondNum"
                                        minLength="3"
                                        maxLength="4"
                                        size="5px"
                                        id="secondNum"
                                        onChange={phoneFunc}
                                    />
                                    -
                                    <input
                                        type="text"
                                        name="lastNum"
                                        minLength="4"
                                        maxLength="4"
                                        size="5px"
                                        id="thirdNum"
                                        onChange={phoneFunc}
                                    />

                                </td>
                            </tr>
                            <tr>
                                <th>
                                     <span>*</span>
                                </th>
                                <td>
                                    <input
                                        type="email"
                                        name="email"
                                        size="30px"
                                        id="email"
                                        onChange={handleChange}
                                    />
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </figure>
                <div className="joinButton">
                    <button type="submit" onClick={dataSubmit}>
                        
                    </button>
                </div>
            </form>
        </>
    );
};

export default JoinForm;