import React, { useEffect } from 'react';

const { kakao } = window;
function MapComponent() {
    useEffect(() => {
        var container = document.getElementById('map'); //   DOM 
        var options = {
            //     
            center: new kakao.maps.LatLng(37.3496888, 127.1069946), // .
            level: 3, // (,  )
        };
        var map = new kakao.maps.Map(container, options); //    
        //   
        var markerPosition = new kakao.maps.LatLng(37.3496888, 127.1069946);

        //  
        var marker = new kakao.maps.Marker({
            position: markerPosition,
        });

        //     
        marker.setMap(map);

        //           
        let mapTypeControl = new kakao.maps.MapTypeControl();

        //     
        // kakao.maps.ControlPosition     TOPRIGHT   
        map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

        //          
        let zoomControl = new kakao.maps.ZoomControl();
        map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);

        let iwContent =
                '<div style="padding:5px;"><p style="font-weight: bold; font-size: 25px; margin-bottom: 10px">Tipsy</p><p style="margin-bottom: 5px;">    46 5</p><a href="https://map.kakao.com/link/map/Tipsy,37.3503814,127.1072135" style="color:blue; font-size: 15px;" target="_blank"></a> <a href="https://map.kakao.com/link/to/Tipsy,37.3503814,127.1072135" style="color:blue; font-size: 15px;" target="_blank"></a></div>',
            iwPosition = new kakao.maps.LatLng(37.3496888, 127.1069946); //  

        //  
        let infowindow = new kakao.maps.InfoWindow({
            position: iwPosition,
            content: iwContent,
        });

        //    .   marker     
        infowindow.open(map, marker);
    }, []);

    return (
        <>
            <div id="map"></div>
        </>
    );
}

export default MapComponent;
