    const navigate = useNavigate();
    //     
    const [isCheckedAll, setIsCheckedAll] = useState(false);
    //      
    const [checkItems, setCheckItems] = useState([]);
    //   
    const [errorMessage ,setErrorMessage] = useState({
        agree_use: null,
        agree_personal : null,
        agree_age : null,
    }); //    

    //   /  
    const handleAllCheck = (checked) => {
        setIsCheckedAll(checked);
        if (checked) {
            //   
            setCheckItems(['agree_use', 'agree_personal', 'agree_age']);
            //    
            setErrorMessage({
                agree_use: null,
                agree_personal : null,
                agree_age : null,
            });

        } else {
            //   
            setCheckItems([]);
        }
    };

    //     
    const handleItemCheck = (itemId) => {
        if (checkItems.includes(itemId)) {
            //     
            setCheckItems(checkItems.filter((item) => item !== itemId));

        } else {
            //     
            setCheckItems([...checkItems, itemId]);
            setErrorMessage((prevError)=>({
                ...prevError,
                [itemId]: null
            }));
        }

    };

    //        on/off
    useEffect(()=>{
        if(checkItems.length>=3){
            setIsCheckedAll(true);
            console.log(isCheckedAll);
        }else{
            setIsCheckedAll(false);
        }
    },[checkItems])

    //   
    const nextPage = (e)=>{
        e.preventDefault();
        if(checkItems.length>=3){
            navigate("/joinform");
        }else{
            Object.keys(errorMessage).forEach((key) => {
                if(!checkItems.includes(key))
                setErrorMessage((preError)=>({
                    ...preError,
                    [key] : ' .',
                }))
            });
        }
    }

