import { Link } from 'react-router-dom/dist';
import '../styles/Header.scss';

function header({ isLoggedIn, handleLogout }) {
    return (
        <>
            <div className="headertop_cover">
                <div className="headerTop">
                    <a href="/" className="mainLogo">
                        tipsy
                    </a>
                    <div className="serviceTab">
                        <ul className="serviceTabList">
                            {isLoggedIn ? (
                                <>
                                    <li>
                                        <Link to="/" onClick={handleLogout}>
                                            
                                        </Link>
                                    </li>
                                    <li>
                                        <Link to="/shopbasket"></Link>
                                    </li>
                                    <li>
                                        <Link to="/customerpage"></Link>
                                    </li>
                                </>
                            ) : (
                                <>
                                    <li>
                                        <Link to="/login"></Link>
                                    </li>
                                    <li>
                                        <Link to="/join"></Link>
                                    </li>
                                    <li>
                                        <Link to="/shopbasket"></Link>
                                    </li>
                                    <li>
                                        <Link to="/customerpage"></Link>
                                    </li>
                                </>
                            )}
                        </ul>
                    </div>
                </div>
            </div>
            <div className="header_cover">
                <div className="headerMenu">
                    <ul className="mainCategoryList">
                        <li className="bannerCategory">
                            <Link to="/mainpage"></Link>
                            <div className="detailCategoryList">
                                <ul>
                                    <li className="hoverCategory">
                                        <Link to="/mainpage?p_category=1">
                                            
                                        </Link>
                                        <ul className="detailCategoryListof">
                                            <li>
                                                <Link to="/mainpage"></Link>
                                            </li>
                                            <li>
                                                <Link to="/mainpage"></Link>
                                            </li>
                                            <li>
                                                <Link to="/mainpage">
                                                    , 
                                                </Link>
                                            </li>
                                            <li>
                                                <Link to="/mainpage">
                                                    , 
                                                </Link>
                                            </li>
                                            <li>
                                                <Link to="/mainpage">
                                                    
                                                </Link>
                                            </li>
                                        </ul>
                                    </li>
                                    <li className="hoverCategory">
                                        <Link to="/mainpage"></Link>
                                    </li>
                                </ul>
                            </div>
                        </li>
                        <li>
                            <Link to="/map"></Link>
                        </li>
                        <li>
                            <Link to="/eventboard"></Link>
                        </li>
                    </ul>
                    <div className="mainSearch">
                        <form action="/searchresult">
                            <input
                                type="search"
                                name="mainSearch"
                                placeholder=""
                            />
                            <button type="submit">
                                <img
                                    src={require('../assets/home_img/search.png')}
                                    alt=""
                                />
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        </>
    );
}

export default header;
