import { React, useState } from 'react';
import '../styles/Detail.scss';
import { Link } from 'react-router-dom';

function Detail() {
    const [pieces, setPieces] = useState(0); //   -  
    const [viewOptionView, setViewOptionView] = useState(false);
    const [popupMessage, setPopupMessage] = useState(false);
    const [bottomPopupMessage, setBottomPopupMessage] = useState(false);

    //  HTML
    return (
        <div id="detail_container">
            <div className="contents">
                <div className="detailScriptPage">
                    <LeftSide />
                    <SideBar />
                </div>
            </div>
        </div>
    );

    // LeftSide HTML
    function LeftSide() {
        return (
            <div className="leftSide">
                <Title />
                <Description />
                <ReviewWRap />
            </div>
        );
    }

    function Title() {
        return (
            <div className="flex">
                <div className="detail_tit_img">
                    <img
                        src="https://d38cxpfv0ljg7q.cloudfront.net/admin_contents/detail/TOnv-1655794504098-1.jpg"
                        alt=""
                    />
                    {/* <img src=${item.img} alt=${item.title} /> */}
                </div>
                <div className="detail_tit">
                    <div className="detail_tit_top opa5">
                        <span>{item.title}</span>
                    </div>
                    <div className="detail_tit_mid opa5">{item.titDetail}</div>
                    <div className="detail_tit_bot opa5">{item.titTag}</div>
                    <div className="detail_tit_star opa5">
                        <div></div>
                    </div>
                    <div>
                        <div className="font_weight">: {item.type}</div>
                        <div className="font_weight">: {item.alc}%</div>
                        <div className="font_weight">
                            : {item.capacity}ml
                        </div>
                        <div className="opa5">: 2  </div>
                    </div>
                    <div>:</div>
                    <div className="price">
                        <span>{item.price.toLocaleString('ko-KR')}</span>
                    </div>
                    <div className="blue opa5">:  2</div>
                    <div className="blue opa5">: </div>
                </div>
            </div>
        );
    }

    function Description() {
        return (
            <div className="detailScript">
                <img
                    src={require('../assets/detail_img/donghak_detail.png')}
                    alt=""
                />
                {/* <img src={item.detailScript} alt='' /> */}
                <img src={require('../assets/detail_img/kakaotalk.png')} />
            </div>
        );
    }

    function ReviewWRap() {
        return (
            <div className="review-wrap">
                <ReviewFilter />
                <ReviewDetail />
            </div>
        );
    }

    function ReviewFilter() {
        return (
            <div className="review_filter">
                <div>
                    <input id="review-checkbox" type="checkbox" />
                    <label htmlFor="review-checkbox"> </label>
                </div>
                <div>
                    <select>
                        <option value=" "> </option>
                        <option value=""></option>
                        <option value="  ">  </option>
                        <option value="  ">  </option>
                    </select>
                </div>
            </div>
        );
    }

    function ReviewDetail() {
        return (
            <>
                {reviewItem.map((item, i) => (
                    <>
                        <div
                            className={`review_detail review${i}`}
                            key={`review + ${i}`}
                        >
                            <div className="review_header">
                                <div className="review_header_name">
                                    {item.user}
                                </div>
                                <div className="review_header_right">
                                    <div>
                                        <div className="review_header_tit">
                                            {item.product}
                                        </div>
                                        <div className="review_star"></div>
                                        <div className="review_date">
                                            {item.date}
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="review_txt">{item.contents}</div>
                        </div>

                        <style jsx>{`
                            .review${i} .review_star::after {
                                content: '${StarScore(item.star)}';
                            }
                        `}</style>
                    </>
                ))}
            </>
        );
    }

    // Review -   
    function StarScore(star) {
        switch (star) {
            case 1:
                return '      ';
            case 2:
                return '      ';
            case 3:
                return '      ';
            case 4:
                return '      ';
            case 5:
                return '      ';
            default:
                return '      ';
        }
    }

    // SideBar HTML
    function SideBar() {
        return (
            <>
                <RightSide />
                <BottomSide />
            </>
        );
    }

    function RightSide() {
        return (
            <div className="rightSide">
                <div className="rightFloat">
                    <form>
                        <OrderForm />
                        <div className="guideofShipment">
                            <p>
                                () 3,000
                                <br />
                                (  ) 3,000
                                <br />
                                <b>50,000  </b>
                            </p>
                        </div>
                        <div className="productBuy_btn">
                            <BuyBtn where="r" />

                            <div
                                className={`popupBasket ${
                                    popupMessage ? '' : 'displayNone'
                                }`}
                            >
                                <CartPopupMessage where="r" />
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        );
    }

    function BottomSide() {
        return (
            <div div className="bottomSide">
                <div className="bottomFloat">
                    <BuyBtn where="b" />
                </div>

                <div
                    className={`bottomPopupBasket ${
                        bottomPopupMessage ? '' : 'displayNone'
                    }`}
                >
                    <CartPopupMessage where="b" />
                </div>
                <OptionView />
            </div>
        );
    }

    function OptionView() {
        return (
            <div className={`optionView ${viewOptionView ? '' : 'unView'}`}>
                <div>
                    <OrderForm />
                    <span
                        className="viewClose"
                        onClick={() => {
                            setViewOptionView(false);
                        }}
                    >
                        <img src="../assets/detail_img/down.svg" alt="" />
                    </span>
                </div>
            </div>
        );
    }

    //   Form 
    function OrderForm() {
        return (
            <>
                <div>
                    <div className="label">
                        <label></label>
                    </div>
                    <select
                        className="optOfProduct"
                        name="option"
                        required
                        defaultValue="default"
                    >
                        <option value="default">{item.title}</option>
                        {/* <option value="soju"></option>
                                    <option value="beer"></option>
                                    <option value="wine"></option> */}
                    </select>
                </div>
                <div>
                    <div className="label">
                        <label></label>
                    </div>
                    <div className="numofProduct">
                        <div
                            className="numfor minus"
                            onClick={() => {
                                if (pieces > 1) setPieces(pieces - 1);
                            }}
                        >
                            -
                        </div>
                        <input
                            type="text"
                            id="pieces"
                            pattern="\d*"
                            value={pieces}
                            readOnly
                        />
                        <div
                            className="numfor plus"
                            onClick={() => {
                                setPieces(pieces + 1);
                            }}
                        >
                            +
                        </div>
                    </div>
                </div>
                <div>
                    <div className="label">
                        <label> </label>
                    </div>
                    <div className="productPrice">
                        <span>
                            {(item.price * pieces).toLocaleString('ko-KR')}
                        </span>
                        
                    </div>
                </div>
            </>
        );
    }

    //     
    function BuyBtn({ where }) {
        return (
            <>
                <div
                    onClick={() => {
                        where === 'r'
                            ? setPopupMessage(true)
                            : setBottomPopupMessage(true);
                    }}
                >
                    
                </div>
                <button
                    onClick={() => {
                        onClickSubmit(where);
                    }}
                >
                    
                </button>
            </>
        );
    }

    //    
    function CartPopupMessage({ where }) {
        function whereis(where) {
            if (where === 'r') return setPopupMessage(false);
            else return setBottomPopupMessage(false);
        }

        return (
            <>
                <div className="popupBasketCon">
                    <p>
                           .
                        <br />
                         ?
                    </p>
                    <div className="popupBasketBtn">
                        <Link to="#" onClick={e => { e.preventDefault(); whereis(where) }}> </Link>
                        <Link to="/shopbasket"> </Link>
                    </div>
                </div>
                <span
                    className="popupClose"
                    onClick={() => {
                        whereis(where);
                    }}
                >
                    X
                </span>
                <span></span>
            </>
        );
    }

    //  Button    
    function onClickSubmit(where) {
        if (where === 'b' && !viewOptionView) return setViewOptionView(true);
        else return formSubmit();
    }

    function formSubmit() {
        console.log(' title, price    ');
    }
}

//    
const item = {
    title: ' 1957',
    titDetail: '       ',
    titTag: '#   #    ',
    type: '',
    alc: 13.0,
    capacity: 375,
    price: 2988,
};

const reviewItem = [
    {
        user: '',
        product: item.title,
        star: 4,
        date: '2022.12.23',
        contents:
            '  .      .          .        .',
    },
    {
        user: '',
        product: item.title,
        star: 2,
        date: '2022.12.23',
        contents: '  .',
    },
];

export default Detail;
